.pagination > li {
    display: inline-block;
    padding-left: 0;
    margin-top: 7px;
  }
  .pagination > li {
    list-style: none;
  }
  .pagination > li > a,
.pagination > li > span {
  position: relative;
  float: left;
  padding: 4px 12px;
  line-height: 1.5;
  color: #1e3992;
  background-color: #fff;
  border: 1px solid #ddd;
  height: 33px;
}
.pagination>li.active>a {
    color: #fff;
    background-color: #1e3992;
    border-color:#1e3992;
  }

.nextoutstanding_AnimationBtn {
    /* position: relative; */
    /* background: transparent; */
    /* padding: 20px 40px; */
    text-transform: uppercase;
    /* text-decoration: none; */
    letter-spacing: 2px;
    /* font-size: 20px; */
    /* color: #fff; */
    /* color: #000; */
    /* border:none; */
    /* cursor:pointer; */
    margin: 10px 20px 0px 30px;
    z-index: 0;
  }
.nextoutstanding_AnimationBtn::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 50%;
    left: 0;
    top: -5px;
    background: #3356d1;
    transform: skewX(30deg);
    z-index: -1;
    transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }
     .nextoutstanding_AnimationBtn:hover::before {
    transform: skewX(0deg);
    left: 50%;
    top: 0;
  }
  .nextoutstanding_AnimationBtn::after {
    content: "";
    position: absolute;
    height: 100%;
    width: 50%;
    left: 51%;
    top: 5px;
    background-color: #6664e9;
    transform: skewX(30deg);
    z-index: -1;
    transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }
  .nextoutstanding_AnimationBtn:hover::after {
    transform: skewX(0deg);
    left: 0%;
    top: 0;
  } 
