@media screen and (max-width:1200px) {
    /* .card-div {
        width: 70%;
        margin: auto;
    } */

    .main_container {
        min-height: 77.3vh;
    }
}

@media screen and (min-height:1000px) {
    .main_container {
        min-height: 90vh;
    }
}

@media screen and (min-height:850px) {
    .main_container {
        min-height: 85vh;
    }
}

@media screen and (max-width:800px) {
    /* .card-div {
        width: 90%;
        margin: auto;
    } */

    .main_container {
        min-height: 87.3vh;
        padding: 0px 15px;
    }
    .main-inner-container{
        margin: 0px 15px;
    }

    .page-type-head1 {
        display: none;
    }
}

@media screen and (min-height:735px) {
    .main_container {
        min-height: 79vh;
    }


}

@media screen and (max-width:550px) {
    .main_container {
        padding: 0px 5px;
    }
    .main-inner-container{
        margin: 0px 5px;
    }
}

@media screen and (max-width:280px) {
    .btn-voilet {
        width: 65px;
        padding-left: 8px;
    }

    .main_container {
        min-height: 85.3vh;
    }
}